import OfficialList from '@/components/menu/DiscoverMusic/DiscoverRankingList/OfficialList.vue';
import GlobalList from '@/components/menu/DiscoverMusic/DiscoverRankingList/GlobalList.vue';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      loadingOk1: 0,
      loadingOk2: 0
    };
  },

  methods: {
    OfficialListLoadingOk(isOk) {
      this.loadingOk1 = isOk;
    },

    GlobalListLoadingOk(isOk) {
      this.loadingOk2 = isOk;
    }

  },
  components: {
    OfficialList,
    GlobalList
  },
  watch: {
    loadingOk1() {
      if (this.loadingOk1 == 1 && this.loadingOk2 == 1) this.isLoading = 0;
    },

    loadingOk2() {
      if (this.loadingOk1 == 1 && this.loadingOk2 == 1) this.isLoading = 0;
    }

  }
};