import bus from '@/components/home/eventBus'; // 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 

import store from '@/store/index.js';
import { getAllLists } from '@/request/api/discoverMusic.js';
export default {
  data() {
    return {
      // 所有榜单
      allLists: [],
      // 全球榜
      globalList: []
    };
  },

  async mounted() {
    // 在 Local Storage(本地存储) 中检查是否有；在 OfficialList.vue 组件中已经获取，但在这里还是需要重新获取，因为未渲染获取
    let vuelsAllLists = this.$ls.get('getAllLists', 'null'); // console.log('vuelsAllLists', vuelsAllLists);
    // 在本地中，不用重新获取

    if (vuelsAllLists != 'null') this.allLists = vuelsAllLists.data.list; // 本地已删除，则重新获取

    if (vuelsAllLists == 'null') {
      // 获取所有榜单
      let res = await getAllLists(); // console.log(res);
      // 转存到本地，有效期为 10 分钟

      this.$ls.set('getAllLists', res, store.state.validityTime);
      this.allLists = res.data.list; // console.log(this.allLists);
    } // 获取全球榜 slice(4,38) 截取包含下标 4，不含 38


    this.globalList = this.allLists.slice(4, this.allLists.length); // console.log(this.globalList);
  },

  methods: {
    // 显示播放键
    playKeyMouseover(index) {
      this.$refs.playKey[index].style.display = "block";
    },

    // 隐藏播放键
    playKeyMouseleave(index) {
      this.$refs.playKey[index].style.display = "none";
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 去往歌单详情页
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 1
        }
      });
    }

  },
  watch: {
    // 标志已请求完数据
    globalList() {
      if (this.globalList.length !== 0) this.$emit('GlobalListLoadingOk', 1);
    }

  }
};