import bus from '@/components/home/eventBus'; // 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 

import store from '@/store/index.js';
import { getAllLists } from '@/request/api/discoverMusic.js';
import { getPlaylistDetail } from '@/request/api/home.js';
export default {
  data() {
    return {
      // 零
      zero: 0,
      // 暂存值
      temp: [],
      // 所有榜单
      allLists: [],
      // 官方榜(包括飙升榜，新歌榜，原创榜，热歌榜)
      officialList: [],
      // 临时存储对象
      temporaryTopFive: {},
      // 官方榜的前五首，数组存储着20首，即4个榜单的前五首
      officialListTopFive: [],
      // 飙升榜的前五首
      soaringListTopFive: [],
      // 飙升榜前五首飙升率
      soaringRate: [],
      // 新歌榜的前五首
      newSongListTopFive: [],
      // 原创榜的前五首
      originalListTopFive: [],
      // 热歌榜的前五首
      hotSongListTopFive: []
    };
  },

  async mounted() {
    // 在 Local Storage(本地存储) 中检查是否有
    let vuelsAllLists = this.$ls.get('getAllLists', 'null'); // console.log('vuelsAllLists', vuelsAllLists);
    // 在本地中，不用重新获取

    if (vuelsAllLists != 'null') this.allLists = vuelsAllLists.data.list; // 本地已删除，则重新获取

    if (vuelsAllLists == 'null') {
      // 获取所有榜单
      let res = await getAllLists(); // console.log(res);
      // 转存到本地，有效期为 10 分钟

      this.$ls.set('getAllLists', res, store.state.validityTime);
      this.allLists = res.data.list; // console.log(this.allLists);
    } // 把飙升榜，新歌榜，原创榜，热歌榜转存到官方榜中


    for (var i = 0; i < 4; i++) {
      this.officialList[i] = this.allLists[i];
    } // console.log('officialList', this.officialList);
    // 获取飙升榜，新歌榜，原创榜，热歌榜 4个歌单


    for (var i = 0; i < 4; i++) {
      // getPlaylistDetail(id) 通过 id 获取指定歌单
      let res = await getPlaylistDetail(this.officialList[i].id); // console.log(res);

      for (var j = 0; j < 5; j++) {
        // 提取前五首
        this.temporaryTopFive = res.data.playlist.tracks[j]; // officialListTopFive[] '官方榜的前五首'数组存储着20首，即4个榜单的前五首

        this.officialListTopFive.push(this.temporaryTopFive);
      }
    } // 分别截取到各榜
    // 截取飙升榜的前五首 slice(0,5) 截取包含下标 0，不含 5


    this.soaringListTopFive = this.officialListTopFive.slice(0, 5); // 截取新歌榜的前五首

    this.newSongListTopFive = this.officialListTopFive.slice(5, 10); // 截取原创榜的前五首

    this.originalListTopFive = this.officialListTopFive.slice(10, 15); // 截取热歌榜的前五首

    this.hotSongListTopFive = this.officialListTopFive.slice(15, 20); // 获取飙升榜

    let res1 = await getPlaylistDetail(this.officialList[0].id);
    this.temp = res1.data.playlist.trackIds.slice(0, 5);

    for (var i = 0; i < 5; i++) {
      // 获取飙升榜的飙升率
      this.soaringRate.push(this.temp[i].ratio);
    } // console.log(this.soaringRate);
    // 获取新歌榜


    let res2 = await getPlaylistDetail(this.officialList[1].id); // console.log(res2);
    // 获取原创榜

    let res3 = await getPlaylistDetail(this.officialList[2].id); // console.log(res3);
  },

  methods: {
    // 获取歌单更新日期
    getUpdateTime(item) {
      // 把返回的时间戳转为时间格式
      var date = new Date(item.updateTime);
      var M = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
      var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      return M + '月' + D + '日更新';
    },

    // 显示播放键
    headMouseover(index) {
      this.$refs.playKey[index].classList = 'playBox';
    },

    // 隐藏播放键
    headMouseleave(index) {
      this.$refs.playKey[index].classList = '';
    },

    // 歌名 title
    songNameTitle(item) {
      if (item.tns) return item.name + "（" + item.tns[0] + "）";
      if (!item.tns && item.alia != '') return item.name + "（" + item.alia[0] + "）";
      return item.name;
    },

    // 去往歌单详情页
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 1
        }
      });
    }

  },
  watch: {
    // 标志已请求完数据
    hotSongListTopFive() {
      if (this.hotSongListTopFive.length === 5) this.$emit('OfficialListLoadingOk', 1);
    }

  }
};